    // ---------------------------------------------------------------------------
    async checkConnection() {
        return await this.http
            .get(this.getBaseUrl())
            .pipe(
                timeout(5000),
                map((__) => true)
            )
            .toPromise()
            .catch((err: HttpErrorResponse) => {
                this.errorMessage = err.message;
                return false;
            })
            .catch((err) => false);
    }

